<template>
  <div class="market-data-container">
    <h2 class="text-h4 text-center q-mb-sm">신마법의대륙 시세표</h2>

    <q-table
      v-if="!loading"
      :rows="sortedMarketData"
      :columns="columns"
      row-key="item"
      :loading="loading"
      :rows-per-page-options="[0]"
      :pagination="{ rowsPerPage: 0 }"
      @row-click="onRowClick"
      class="styled-table"
      :table-style="{ fontSize: '16px' }"
      :table-col-style="tableColStyle"
      flat
      bordered
      hide-bottom
      separator="none"
    >
    </q-table>

    <q-dialog v-model="showDialog" @hide="onDialogHide">
      <q-card style="min-width: 350px; max-width: 600px; width: 90vw">
        <q-card-section class="item-title-wrap">
          <div class="item-title-text">
            {{ selectedItem ? selectedItem.item : "" }} 거래내역
          </div>
        </q-card-section>

        <q-card-section v-if="selectedItem" class="price-info-wrap">
          <div class="text-subtitle2">가격 정보 (개당)</div>
          <q-list dense>
            <q-item>
              <q-item-section>
                <q-item-label
                  >최고가 : {{ formatPrice(highestPrice) }}원
                </q-item-label>
              </q-item-section>
            </q-item>

            <q-item>
              <q-item-section>
                <q-item-label
                  >최저가 : {{ formatPrice(lowestPrice) }}원
                </q-item-label>
              </q-item-section>
            </q-item>

            <q-item>
              <q-item-section>
                <q-item-label
                  >평균가 : {{ formatPrice(selectedItem.averagePrice) }}원
                </q-item-label>
              </q-item-section>
            </q-item>
          </q-list>
        </q-card-section>

        <q-card-section class="price-history-wrap">
          <div class="text-subtitle2">거래 내역</div>
          <q-list dense>
            <q-item
              v-for="(transaction, index) in selectedItem?.transactions"
              :key="index"
            >
              <q-item-section>
                <q-item-label
                  >{{ transaction.date }}

                  <span class="average-count-text">
                    {{ formatNumber(transaction.count) }}개
                  </span>

                  {{ formatPrice(transaction.price) }}원
                  <span class="average-price-text">
                    (개당
                    {{
                      formatPrice(
                        getPricePerUnit(transaction.price, transaction.count)
                      )
                    }}원)
                  </span>
                </q-item-label>
              </q-item-section>
            </q-item>
          </q-list>
        </q-card-section>

        <q-card-actions align="right" class="text-primary item-footer-wrap">
          <q-btn flat label="닫기" v-close-popup />
        </q-card-actions>
      </q-card>
    </q-dialog>
  </div>
</template>

<script setup>
import { ref, onMounted, computed } from "vue";
import axios from "axios";

const sheetData = ref([]);
const processedData = ref([]);
const loading = ref(true);
const error = ref(null);

const tableColStyle = [{ width: "60%" }, { width: "15%" }, { width: "25%" }];

const columns = [
  {
    name: "item",
    label: "아이템명",
    field: "item",
    sortable: true,
  },
  {
    name: "totalCount",
    label: "거래량",
    field: "totalCount",
    sortable: true,
    format: (val) => formatNumber(val),
  },
  {
    name: "averagePrice",
    label: "평균가격",
    field: "averagePrice",
    sortable: true,
    format: (val) => formatPrice(val),
  },
];

const sortedMarketData = computed(() => {
  return [...processedData.value].sort((a, b) => b.totalCount - a.totalCount);
});

const processData = (data) => {
  const groupedData = data.reduce((acc, item) => {
    const matches = item.item.match(/\((\d+)개\)$/);
    let count = 1;
    let processedItem = item.item;

    if (matches) {
      count = parseInt(matches[1]);
      processedItem = item.item.replace(/\(\d+개\)$/, "").trim();
    }

    const price = parseInt(item.price.replace(/,/g, ""));

    if (!acc[processedItem]) {
      acc[processedItem] = {
        item: processedItem,
        totalCount: 0,
        totalPrice: 0,
        transactions: [],
      };
    }

    acc[processedItem].totalCount += count;
    acc[processedItem].totalPrice += price;
    acc[processedItem].transactions.push({
      date: item.date,
      time: item.time,
      buyer: item.buyer,
      price: price,
      count: count,
    });

    return acc;
  }, {});

  return Object.values(groupedData).map((group) => ({
    ...group,
    averagePrice: Math.round(group.totalPrice / group.totalCount),
  }));
};

const fetchSheetData = async () => {
  const SHEET_ID = process.env.VUE_APP_SHEET_ID;
  const SHEET_NAME = process.env.VUE_APP_SHEET_NAME;
  const SHEET_RANGE = process.env.VUE_APP_SHEET_RANGE;
  const API_KEY = process.env.VUE_APP_API_KEY;

  const url = `https://sheets.googleapis.com/v4/spreadsheets/${SHEET_ID}/values/${SHEET_NAME}!${SHEET_RANGE}?key=${API_KEY}`;

  try {
    const response = await axios.get(url);
    const values = response.data.values;

    // 첫 번째 행은 헤더이므로 제외하고 데이터를 처리합니다
    sheetData.value = values.slice(1).map((row) => ({
      date: row[0],
      time: row[1],
      item: row[2],
      buyer: row[3],
      price: row[4],
    }));
  } catch (err) {
    console.error("Error fetching sheet data:", err);
    error.value = "데이터를 불러오는 중 오류가 발생했습니다.";
  } finally {
    loading.value = false;
  }
};

const showDialog = ref(false);
const selectedItem = ref(null);

const onRowClick = (evt, row) => {
  selectedItem.value = row;
  showDialog.value = true;
};

const onDialogHide = () => {
  selectedItem.value = null;
};

// 개당 가격을 계산하는 함수 추가
const getPricePerUnit = (price, count) => {
  return price / count;
};

const highestPrice = computed(() => {
  if (!selectedItem.value) return 0;
  return Math.max(
    ...selectedItem.value.transactions.map((t) => Math.round(t.price / t.count))
  );
});

const lowestPrice = computed(() => {
  if (!selectedItem.value) return 0;
  return Math.min(
    ...selectedItem.value.transactions.map((t) => Math.round(t.price / t.count))
  );
});

// 가격을 정수로 포맷팅하는 함수
const formatPrice = (price) => {
  return new Intl.NumberFormat("ko-KR").format(Math.round(price));
};

// 숫자를 정수로 포맷팅하는 함수 (거래량 등에 사용)
const formatNumber = (number) => {
  return new Intl.NumberFormat("ko-KR").format(Math.round(number));
};

onMounted(async () => {
  await fetchSheetData();
  processedData.value = processData(sheetData.value);
});
</script>

<style lang="scss" scoped>
.market-data-container {
  padding: 8px;

  max-width: 600px;
  margin: 0 auto;
}

.item-title-text {
  font-size: 20px;
}

.text-subtitle2 {
  font-size: 16px;
}

.price-info-wrap,
.price-history-wrap {
  gap: 4px;
  display: flex;
  flex-direction: column;
}
// .price-history-wrap {
//   max-height: 200px;
//   overflow: auto;
// }

.average-count-text {
  margin: 0 24px;
}

.average-price-text {
  font-size: 10px;
  color: rgb(48, 128, 233);
}

.item-title-wrap {
  border-bottom: 1px solid #ccc;
}

.item-footer-wrap {
  border-top: 1px solid #ccc;
}
// @media (max-width: 500px) {

// }
</style>
